<template>
    <el-dialog :title="propObj?'':''"  size="small" ref="box" v-model="showDialog">
        <el-form ref="form" label-width="100px">
            <el-form-item label="" style="text-align: center;vertical-align: middle">
                <img width="60" height="60" :src="obj.photo?obj.photo:''" id="showimg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <a  href="javascript:void(0)" class="file" style="display: inline-block;top: -15px">
                    <input type="file"  v-imgfile="'showimg'" id="file">
                </a>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input  style="width: 80%" name="name" v-model="obj.name"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input  style="width: 80%" name="password" v-model="obj.password"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input  style="width: 80%" name="age" v-model="obj.age"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-select v-model="obj.sex" style="width: 80%" name="sex">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input  style="width: 80%" name="company" v-model="obj.company"></el-input>
            </el-form-item>
            <el-form-item label="qq" style="text-align: center">
                <el-input style="width: 80%" name="qq" v-model="obj.qq"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input style="width: 80%" name="email" v-model="obj.email"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-switch v-model="obj.state" on-color="#13ce66" off-color="#ff4949" :on-value="1" :off-value="0"></el-switch>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input style="width: 80%" name="question" v-model="obj.question"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: center">
                <el-input style="width: 80%" name="answer" v-model="obj.answer"></el-input>
            </el-form-item>
        </el-form>
        <el-row class="dialog-footer" slot="footer">
            <el-button type="primary" @click="save" :laoding="savePending">
                
            </el-button>
        </el-row>
    </el-dialog>
</template>

<script>
    var imgFile=require("../../director/imgFile")
    module.exports={
        props:["propObj"],
        data:function () {
            return {
                obj:this.propObj?(function() {
                    if(this.propObj.sex!="" && this.propObj.sex!="")
                    {
                        this.propObj.sex="";
                    }
                    return this.propObj
                }).call(this):{
                    name:"",
                    password:"",
                    photo:"",
                    age:"",
                    sex:"",
                    qq:"",
                    email:"",
                    company:"",
                    state:1,
                    question:"",
                    answer:""
                },
                savePending:false,
                showDialog:false
            }
        },
        directives:{
            "imgfile":imgFile,
        },
        computed:{

        },
        methods:{
            save:function () {
                var _this=this;
                var obj={};
                if(this.propObj)
                {
                    obj.id=this.propObj._id;
                }
                if($.query("#file").value)
                {
                    obj.photo=$.query("#file").files[0];
                }
                obj.age=(this.obj.age!==undefined && this.obj.age!==null)?this.obj.age:"";
                obj.sex=(this.obj.sex!==undefined && this.obj.sex!==null)?this.obj.sex:""
                obj.company=(this.obj.company!==undefined && this.obj.company!==null)?this.obj.company:""
                obj.qq=(this.obj.qq!=undefined && this.obj.qq!==null)?this.obj.qq:""
                obj.email=(this.obj.email!==undefined && this.obj.email!==null)?this.obj.email:""
                if(this.obj.question)
                {
                    obj.question=this.obj.question
                }
                else
                {
                    $.tip("",0);
                    return;
                }
                if(this.obj.answer)
                {
                    obj.answer=this.obj.answer
                }
                else
                {
                    $.tip("",0);
                    return;
                }
                if(this.obj.name)
                {
                    obj.name=this.obj.name
                }
                else
                {
                    $.tip("",0);
                    return;
                }
                if(this.obj.password)
                {
                    obj.password=this.obj.password
                }
                else
                {
                    $.tip("",0);
                    return;
                }
                obj.state=this.obj.state;
                this.savePending=true;
                this.$store.dispatch("saveUser",obj).then(function (data) {
                    _this.savePending=false;
                    if(data.code==200)
                    {
                        $.notify("",1);
                        _this.$refs.box.close();
                    }
                    else
                    {
                        $.notify(data.msg,0);
                    }
                })
            }
        }
    }
</script>



