<template>
    <el-dialog title=""  size="large" ref="box" v-model="showDialog">
        <el-tabs type="card">
            <el-tab-pane label="" style="height: 500px;overflow-y: auto">
                <table class="table-hover" style="width: 100%;">
                    <thead>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                    </thead>
                    <template v-for="(item,index) in obj.own">
                        <tr style="height: 50px;text-align: center;vertical-align: middle">
                            <td style="width: 20%">
                                {{item.name}}
                            </td>
                            <td style="width: 20%">
                                {{item.dis?item.dis:""}}
                            </td>
                            <td style="width: 20%">
                                {{item.createdAt}}
                            </td>
                            <td style="width: 10%">
                                {{item.userCount}}
                            </td>
                            <td style="width: 10%">
                                {{item.interfaceCount}}
                            </td>
                            <td style="width: 10%">
                                {{item.team?item.team.name:""}}
                            </td>
                            <td style="width: 10%">
                                <el-button type="text" @click="transfer(item,index)" title=""><i class="fa fa-sign-in"></i></el-button>&nbsp;&nbsp;
                                <el-button type="text" icon="close" style="color: red" @click="remove(item,index)" title=""></el-button>
                            </td>
                        </tr>
                    </template>
                </table>
            </el-tab-pane>
            <el-tab-pane label="" style="height: 500px;overflow-y: auto">
                <table class="table-hover" style="width: 100%;">
                    <thead>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    </thead>
                    <template v-for="(item,index) in obj.join">
                        <tr style="height: 50px;text-align: center;vertical-align: middle">
                            <td style="width: 10%">
                                {{item.name}}
                            </td>
                            <td style="width: 10%">
                                {{item.dis?item.dis:""}}
                            </td>
                            <td style="width: 5%">
                                {{item.owner.name}}
                            </td>
                            <td style="width: 10%">
                                {{item.createdAt}}
                            </td>
                            <td style="width: 10%">
                                {{item.userCount}}
                            </td>
                            <td style="width: 10%">
                                {{item.interfaceCount}}
                            </td>
                            <td style="width: 10%">
                                {{item.team?item.team.name:""}}
                            </td>
                            <td style="width: 25%">
                                <el-select v-model="item.role" @input="role(item,index)" style="width: 60%">
                                    <el-option label="" :value="0"></el-option>
                                    <el-option label="" :value="1"></el-option>
                                </el-select>
                                &nbsp;
                                <el-button v-if="item.role==1" type="text" size="small" @click="roleOption(item,index)"></el-button>
                            </td>
                            <td style="width: 10%">
                                <el-button type="text" icon="close" style="color: red" @click="quit(item,index)" title=""></el-button>
                            </td>
                        </tr>
                    </template>
                </table>
            </el-tab-pane>
        </el-tabs>
    </el-dialog>
</template>

<script>
    var roleOption=require("../../component/roleOption.vue")
    module.exports={
        props:["propObj","user"],
        data:function () {
            return {
                showDialog:false,
                obj:this.propObj
            }
        },
        computed:{
            ownTabLabel:function () {
                return "("+this.obj.own.length+")"
            },
            joinTabLabel:function () {
                return "("+this.obj.join.length+")"
            }
        },
        methods:{
            transfer:function (item,index) {
                var _this=this;
                $.input("",function (val) {
                    if(!val.value)
                    {
                        $.tip("",0);
                        return false;
                    }
                    $.startHud();
                    net.put("/admin/userprojectown",{
                        id:item._id,
                        user:val.value
                    }).then(function (data) {
                        $.stopHud();
                        if(data.code==200)
                        {
                            $.notify("",1);
                            _this.obj.own.splice(index,1);
                        }
                        else
                        {
                            $.notify(data.msg,0)
                        }
                    })
                })
            },
            remove:function (item,index) {
                var _this=this;
                $.confirm("",function () {
                    $.startHud();
                    net.delete("/admin/project",{
                        id:item._id
                    }).then(function (data) {
                        $.stopHud();
                        if(data.code==200)
                        {
                            $.notify("",1);
                            _this.obj.own.splice(index,1);
                        }
                        else
                        {
                            $.notify(data.msg,0)
                        }
                    })
                })
            },
            quit:function (item,index) {
                var _this=this;
                $.confirm("",function () {
                    $.startHud();
                    net.put("/admin/userquitproject",{
                        id:item._id,
                        user:_this.user._id
                    }).then(function (data) {
                        $.stopHud();
                        if(data.code==200)
                        {
                            $.notify("",1);
                            _this.obj.join.splice(index,1);
                        }
                        else
                        {
                            $.notify(data.msg,0)
                        }
                    })
                })
            },
            role:function (item,index) {
                var _this=this;
                $.startHud();
                net.post("/admin/projectuserrole",{
                    id:item._id,
                    user:this.user._id,
                    role:item.role
                }).then(function (data) {
                    $.stopHud();
                    if(data.code==200)
                    {
                        $.notify("",1);
                        if(item.role==0)
                        {
                            delete item.option;
                        }
                    }
                    else
                    {
                        $.notify(data.msg,0)
                    }
                })
            },
            roleOption:function (item,index) {
                var _this=this;
                var child=$.showBox(this.$root,roleOption,{
                    data:item.option,
                    hudremove:1
                });
                child.$on("save",function (data) {
                    Vue.set(item,"option",data);
                    net.post("/admin/projectuserrole",{
                        id:item._id,
                        user:_this.user._id,
                        role:item.role,
                        option:JSON.stringify(data)
                    }).then(function (data) {
                        $.stopHud();
                        if(data.code==200)
                        {
                            $.notify("",1);
                        }
                        else
                        {
                            $.notify(data.msg,0)
                        }
                    })
                })
            }
        }
    }
</script>


